import './Navbar.css';
import React from 'react';
import Toggle from '../Toggle/Toggle';
import { Link } from 'react-scroll';

function Navbar () {

  return(
  
    
  
/* Navbar Section */
  
  <div className='navbar'>

    <div className='leftside-header'>
    <div className='logo'>Altaf</div>
     
    </div>
    <Toggle />


    <div className='rightside-header'>


      <div className='menu-btn'>
        <ul>
        <Link to="Intro" smooth={true} spy={true}> <li>About</li> </Link>
        <Link to="services" smooth={true} spy={true}><li>Skills</li> </Link>
        <Link to="portfolio" smooth={true} spy={true}> <li>Projects</li> </Link>
        <Link to="experience" smooth={true} spy={true}> <li>Experience</li> </Link>
        </ul>
      </div>


      <Link to="contact" smooth={true} spy={true}><button className='button n-button'>Connect</button> </Link>
    </div>
  </div>);
}


export default Navbar;