import Facebook from "@iconscout/react-unicons/icons/uil-facebook";
import Gitub from "@iconscout/react-unicons/icons/uil-github";

function Footer (){

    return(
    
        <div className="footer">
        <img src={Wave} alt="" className="wave" style={{ width: "100%" }} />
        <div className="f-content">
          <span>Altafrajavines@gmail.com</span>
          <div className="f-icons">
          
        
        <a href="https://www.instagram.com/altafraja_21/" > <Insta color="white" size={"3rem"} /> </a>
           <a href="https://www.facebook.com/altafraja_21/" ><Facebook color="white" size={"3rem"} /> </a>
            <a href="https://github.com/Altafraja21" > <Gitub color="white" size={"3rem"} /></a>
          </div>
        </div>
      </div>
    
        );

}


export default Footer;