import './Testimonials.css';
import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import { Pagination } from "swiper";
import "swiper/css/pagination";
import profilePic1 from "../../img/profile1.png";
import profilePic2 from "../../img/profile2.jpg";
import profilePic3 from "../../img/profile3.jpg";
import profilePic4 from "../../img/profile4.jpg";
function Testimonials (){



    const clients = [
        {
          img: profilePic1,
          review:
            "I went to Altaf with a basic feel I wanted in a website. he helped flush out the visual layout and content. Altafs services are bargain when you weigh the cost versus the product you receive. I will be using them again in future endeavors. Thanks again!",
        },
        {
          img: profilePic2,
          review:
            "We are very impressed with the all aspects of our new website. We would highly reccomend Altaf to anyone looking to build a new website.",
        },
        {
          img: profilePic3,
          review:
            "My new site created by Altaf just launched. Heve done a great job of creating exactly what I wanted. Easy to work with and very responsive. ",
        },
        {
          img: profilePic4,
          review:
            "Altaf built my small business a great website that really helps current and potential customers get a good feel for my company. ",
        },
      ];














    return(
    
        <div className="t-wrapper" >

        <div className="t-heading">
        <span>Clients always get </span>
        <span>Exceptional Work </span>
        <span>from me...</span>

        <div className="blur t-blur1" style={{ background: "var(--purple)" }}></div>
      <div className="blur t-blur2" style={{ background: "skyblue" }}></div>

      </div>


      <Swiper
        // install Swiper modules
        modules={[Pagination]}
        slidesPerView={1}
        pagination={{ clickable: true }}
      >

{clients.map((client, index) => {
          return (
            <SwiperSlide key={index}>
              <div className="testimonial">
                <img src={client.img} alt="" />
                <span>{client.review}</span>
              </div>
            </SwiperSlide>
          );
        })}

        

        </Swiper>



        </div>
    
    
    
        );
}


export default Testimonials;